.item {
  /* background-color: #5c784c; */
  position: relative;
  min-width: 120px;
  height: 120px;
  overflow: visible;
  border-radius: 20px;
  margin: 3px;
  width: max-content;
  width: 8.5rem;
  width: 7.8rem;
  height: 7.8rem;
  display: inline-block;
}
.canFire {
  position: relative;
  box-shadow: -1px 0px 14px 6px #7ae621;
  background-color: #7ae621;
  /* width: 7.8rem;
  height: 7.5rem; */
}

.skillButton {
  z-index: 2;
  overflow: visible;
  position: absolute;
  top: 0px;
  left: 0;
  margin: 0px 4px 1px 0px;
  width: 3rem;
  /* height: 7.8rem; */
  width: 100%;
  width: 7.5rem;
  height: 7.5rem;
  background-color: #e0ff9d;
  justify-content: center;
  border: transparent;
  border-radius: 20px;
  font-size: 5rem;
  line-height: 2rem;
  cursor: pointer;
  transition: 250ms;

  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.1;
}
.canFire .skillButton {
  z-index: 4;
  /* opacity: 0.04; */
  opacity: 100%;
}
.skillButton:hover,
.skillButton:focus,
.skillButton:active {
  /* z-index: -1; */
  opacity: 0.04;
}

