.bar {
  z-index: 2;
  position: absolute;
  left: 0;
  height: 2.2rem;
  height: 1.9rem;
  width: 7.5rem;
  height: 7.5rem;


  bottom: 0.3rem;
  border-radius: 0 0 20px 20px;

  border-radius: 20px;

  text-align: left;
  overflow: visible;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.text {
  z-index: 2;
  display: block;
  color: #000000a0;
  width: 100%;
  height: 100%;
  font-weight: bold;
  z-index: 3;
  line-height: 0.84rem;
  text-align: center;
}

.name {
  z-index: 2;
  min-width: 120px;
  position: absolute;
  position: relative;
  display: block;
  overflow: hidden;

  bottom: 0rem;
  border-radius: 0 0 20px 20px;

  box-shadow: #ffffffbb 0 -1px 1px 0px;
  background-color: #ffffffbb;

  color: #000000a0;
  height: 1.73rem;
  height: 2.9rem;
  font-weight: bold;
  z-index: 3;
  line-height: 0.84rem;
  text-align: center;
}

.iconBag {
  position: absolute;
  position: relative;
  z-index: 3;
  width: 7.5rem;
  height: 1.5em;
  left: 0;
  transform: scaleX(1.5) scaleY(1.3) translateY(-0.1rem);

  top: 0.1rem;
  justify-content: center;
  align-items: flex-start;
  border-radius: 20px 20px 0 0;

  padding: 0 0.5rem 1rem 0.5rem;
  display: flex;
  box-sizing: border-box;
  overflow: hidden;
  flex-flow: row-reverse;
  flex-wrap: wrap;
  column-gap: 3px;
}
.disabled .iconBag {
  z-index: 999;
}
.canFire .iconBag {
  z-index: 1;
}
.disabled {
  display: block;
  position: relative;
}
.disabled::after {
  z-index: 2;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 7.5rem;
  height: 7.5rem;
  border-radius: inherit;
  background-color: transparent;
}
.spent.disabled::after {
  /* background-color: #ff0000; */
  background-color: #000000;
  opacity: 40%;
}
